import React, { Component } from 'react';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import bkimg from '../assets/ppk.jpg'
import { makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import classes from './navigation.module.css'
import Aos from 'aos'
import 'aos/dist/aos.css'
import { HashLink as Linki } from 'react-router-hash-link';

import {motion} from 'framer-motion'

class Navbar extends Component{

   
  componentDidMount(){
    Aos.init({duration:2000 })
} 
    render(){
      
     return(
            <div style={{alignContent:'center'}}>
<AppBar position="absolute" style={{backgroundColor:'rgba(61,67,71,0)',zIndex:'5'}}>
  <Toolbar >
    <div className={classes.navmenu}>
    <IconButton edge="start" color="inherit" aria-label="menu">
      <MenuIcon />
    </IconButton>
    </div>

   <div className={classes.navbar}>
        
            
   <div className={classes.navitem}>
    <Button color="inherit" component={Linki} smooth to="#home">HOME</Button>
    <Button color="inherit" component={Linki} smooth to="#gallery">VIEW GALLERY</Button>
    <Button color="inherit" component={Linki} smooth to="#contactus">CONTACT US</Button>
    </div>
    </div>
  </Toolbar>
</AppBar>
    <div  data-aos='fade-in'
     style={{boxsizing:"border-box",position:'relative' ,top:'0',width:'100%'
                ,backgroundColor:'rgba(61,67,71,1)',objectFit:'cover',zIndex:'1' }}>
                <img className={classes.img} src={bkimg} alt='wedding'></img>
            </div>
     <div>
         
         
    </div >       
    <div
    data-aos='fade-in'
    className={classes.textbox} >
    <p  data-aos='fade-in' className={classes.textt}>
            Because every photo has a story to tell.
            We don’t capture what the moment looks like,
            We capture what it feels like!

     </p>  
    </div>
     </div>
          
        )
    }
}
export default Navbar