import React,{Component} from 'react'
import TextField from '@material-ui/core/TextField'
import Button from '@material-ui/core/Button'
import classes from './contactus.module.css'
import Aos from 'aos'
import 'aos/dist/aos.css'

class contact extends Component{

    componentDidMount(){
        Aos.init({duration:3000 })
    } 
    render(){
        return(
          <div  style={{display:'flex',width:'100%',height:'100%',backgroundColor:'transparent'
          ,alignContent:'center',boxSizing:'border-box',justifyContent:'space-around',padding:'50px'}}>
            <div data-aos='fade-up' className={classes.container}>
               <h2 className={classes.h2tag}>CONTACT US</h2>
                <form className={classes.form}>
                 <div className={classes.textfield}>
                 <TextField className={classes.textfieldin} variant="outlined" label='enter name'/>
                 </div>
                 <div className={classes.textfield}>
                 <TextField className={classes.textfieldin} variant="outlined" label='phone no.' required/>
                 </div >
                 <div className={classes.textfield}>
                 <TextField  className={classes.textfieldin}variant="outlined" label='email id'/>
                 </div>
                 <div className={classes.textfield}>
                  <Button className={classes.textfieldin}>Submit</Button>
                  </div> 
            </form>    
            </div>
            </div>
           
        )
    }

}
export default contact