import React, { Component } from 'react';
import Aos from 'aos'
import 'aos/dist/aos.css'

class footer extends Component{
   
    componentDidMount(){
        Aos.init({duration:3000 })
    } 
    render(){
        return(
            <div data-aos='fadeup' style={{width:'100vw',height:'20vh',background:'black'}}>
                hello footter
            </div>
        )
    }
}
export default footer