import React,{Component} from 'react'
import {motion} from 'framer-motion'
import f1 from '../assets/f1.jpg'
import Aos from 'aos'
import 'aos/dist/aos.css'
import classes from './featuren.module.css'
import Card from '@material-ui/core/Card'

class Featured extends Component{

    componentDidMount(){
        Aos.init({duration:2000 })
    } 

    render()
    {
        return(
            <div style={{backgroundColor:'rgb(61,67,71)',boxSizing:'border-box',
            display:'flex',flexDirection:'row'
            ,alignItems:'center',padding:'5px' ,
            justifyContent:'space-around',marginTop:'15px'}}>
                <div style={{boxSizing:'border-box',width:'100%'}}>
                 <img  data-aos='fade-right' className={classes.img} src={f1} alt='featured1'/>
                 </div>
                 <div data-aos='flip-left' className={classes.feturetext} >
                            <h5 className={classes.texthead}><strong>HELLO</strong></h5>
                         <p className={classes.text} >
                       Because every photo has a story to 
                       tell. its abavout the story you tell anr 
                       the best you get
                        We don’t capture what the moment looks like,
                        We capture what it feels like!
                         </p>  
                    </div>
            </div>
        )
    }
} 
export default Featured